*/

.Landing {
  background-color: white;
  color: #686868;
  justify-content: center;
  padding: 40px 40px 40px 40px;
}

.top-header {
  float: left;
  padding-bottom: 12px;
  text-align: left;
  color: #87c3b9;
}
.landing-description {
  margin-top: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.landing-header {
  padding-bottom: 12px;
}

.social {
  margin: 16px;
}

/*
	Sea foam: #87c3b9
