.Developer {
  background-color: #87c3b9;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 40px 10px 40px;
}

.developer-description {
  margin-bottom: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

