.main-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 12px;
}

.portfolio-carousel {
  height: 100%;
}
.portfolio-carousel-item {
  width: 75%;
  overflow: hidden;
  align-items: center;
}

.carousel-control-next,
.carousel-control-prev
{
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

