/*
	Sea foam: #87c3b9
	Dark sea foam: #3e7c72
	Light gray: #eeeeee
	Dark gray: #686868
	Mauve: #c3878c
  Background dark: #282c34
*/

.App {
  text-align: center;
}

p, a, h1, h2, h3, h4, h5, h6 {
  font-family: 'Gothic A1', sans-serif;
}

p, a {
  font-size: 15px;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #3e7c72;
  font-weight: bold;
}

a:hover, a:active {
  color: #c3878c !important;
  text-decoration: none;
}

.main-section, .detail-page {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.section-header {
  font-size: calc(32px + 2vmin);
  text-align: center;
  padding-top: 64px;
}

/* DETAIL PAGES */
.detail-column {
  /* max-width: 400px; */
}

.detail-page {
  background-color: white;
  color: #686868;
  padding-bottom: 36px;
}

.detail-row {
  margin: 0 auto;
  /* margin-top: 72px;
  margin-bottom: 72px; */
  text-align: center;
  max-width: 800px;
}

.detail-image {
  max-width:100%;
  max-height:100%;
  padding-bottom: 8px;
  /* width: 350px; */
}

.detail-image-left, .detail-image-right {
  max-width:100%;
  max-height:100%;
  padding-bottom: 8px;
  width: 380px;
}
.detail-image-left {
  float: left;
  padding-left: 0;
}
.detail-image-right {
  float: right;
  padding-right: 0;
}
.detail-image-tight {
  max-width:100%;
  max-height:100%;
  padding: 0;
  /* width: 250px; */
}

.detail-row {
  margin-top: 80px;
  margin-bottom: 64px;
}

.detail-hero-image {
  width:90%;
  height: auto;
  padding: 12px;
}

